
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import  TslHeader  from '@/components/tsl-header/TslHeader';
import { queryNewsList } from '@/api/news';

export default {
data() {
return {
title: '新闻资讯',
position: 'relative',
newsListData: [],
bottomCount: 20,
}
},
components: {
TslHeader,
Scroller,
LoadMore
},
computed: {
},
methods: {
queryData(){
queryNewsList()
.then(res => {
this.newsListData = res.data;
})
.catch(error => {
console.log(error.data)
})
},
goNewsDetail(id) {
this.$router.push({
path: '/news/detail',
query:{
'id':id
}
});
}
},
mounted() {
this.queryData()
}
}



