import objectAssign from 'object-assign'

export default {
    name: 'tsl-header',
    props: {
        leftOptions: Object,
        title: String,
        transition: String,
        rightOptions: {
            type: Object,
            default() {
                return {
                    showMore: false
                }
            }
        },
        color:String,
        position:String
    },
    beforeMount() {
        if (this.$slots['overwrite-title']) {
            this.shouldOverWriteTitle = true
        }
    },
    computed: {
        _leftOptions() {
            return objectAssign({
                showBack: true,
                preventGoBack: false
            }, this.leftOptions || {})
        }
    },
    methods: {
        onClickBack() {
            if (this._leftOptions.preventGoBack) {
                this.$emit('on-click-back')
            } else {
                this.$router ? this.$router.back() : window.history.back()
            }
        }
    },
    data() {
        return {
            shouldOverWriteTitle: false
        }
    }
}



