
import ViewBox from 'vux/src/components/view-box/index.vue'

import  TslHeader  from '@/components/tsl-header/TslHeader';
import { queryNewsDetail } from '@/api/news';

export default {
data() {
return {
title: '新闻资讯',
position: 'relative'
}
},
components: {
ViewBox,
TslHeader
},
computed: {
},
methods:{
queryData(){
/*获取文章id*/
const id = this.$route.query.id;
queryNewsDetail(id)
.then(res => {
this.newsListData = res.data;
})
.catch(error => {

})
}
},
mounted() {
this.queryData()
}
}



