
import ViewBox from 'vux/src/components/view-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import TslHeader from '@/components/tsl-header/TslHeader'
import TslEmpty from '@/components/tsl-empty/TslEmpty'
import Lockr from 'Lockr'
import { login } from '@/api/login'

export default {
name: 'Home',
data() {
return {
title: '会员登录',
username:'高雅静test7',
password:'222222',
showPsw:false,
uri:this.$route.query.uri
}
},
components: {ViewBox, Group, XInput, XButton,TslHeader, TslEmpty},
methods:{
fetchToken(){
login(
this.username,
this.password
)
.then( res => {
console.log(res);
Lockr.set('token',res.token);
this.$router.replace({
path: this.uri
});
})
.catch(error => {
console.warn(error.response.data.message);
this.$vux.toast.show({
text: error.response.data.message,
type: 'cancel'
})
});
},
goBack(){
this.$router.back()
}
},
mounted() {

}
}



